            </b-tab-item>
            <b-tab-item label="Record">
              <TabRecordVideo />
            </b-tab-item>
            <b-tab-item label="Videos">
              <TabVideoFiles />
            </b-tab-item>
            <b-tab-item label="Account">
              <TabAccountChange/>
            </b-tab-item>
        </b-tabs>


    </section>
