</template>


<script>
import Vue from 'vue'

let LoadingComponent = Vue.component('LoadingForAsyncComponent')
let ErrorComponent = Vue.component('ErrorLoadingComponent')


const TabAppWebcamStatus = () => ({
  component: import('../tabs/AppWebcamStatus'),
  loading: LoadingComponent,
  error: ErrorComponent,
  delay: 100,
  timeout: 3000
})

const TabViewLive = () => ({
  component: import('../tabs/ViewLive'),
  loading: LoadingComponent,
  error: ErrorComponent,
  delay: 100,
  timeout: 3000
})

const TabRecordVideo = () => ({
  component: import('../tabs/RecordVideo'),
  loading: LoadingComponent,
  error: ErrorComponent,
  delay: 100,
  timeout: 3000
})

const TabVideoFiles = () => ({
  component: import('../tabs/VideoFiles'),
  loading: LoadingComponent,
  error: ErrorComponent,
  delay: 100,
  timeout: 3000
})

const TabAccountChange = () => ({
  component: import('../tabs/AccountChange'),
  loading: LoadingComponent,
  error: ErrorComponent,
  delay: 100,
  timeout: 3000
})
 

export default {
  name: 'WebControlHomePage',
  props: {
    msg: String
  },
  created(){
  },
  components: {
    TabAccountChange,
    TabAppWebcamStatus,
    TabVideoFiles,
    TabViewLive,
    TabRecordVideo,
  }

}
</script>

